<template>
    <div class="barChart">
        <ve-bar
            :colors="barColors"
            :extend="barExtend"
            :legend-visible="false"
            :tooltip-visible="false">
        </ve-bar>
    </div>
</template>

<script>
/* eslint-disable */
    export default {
        name: 'barChart',
        components: {},
        data() {
            return {
              barColors:['#4085EB'],
              barExtend:{
                yAxis: {
                  type: 'category',
                  // data:['Top10 出现语速过快现象','Top9 出现抢话现象','Top8 答案针对性差', 'Top7 确认不必要信息','Top6 处理出错未纠正','Top5 提醒不到位','Top4 表述不清楚','Top3 禁语/错别字','Top2 礼貌/规范问题','Top1 没理解客户问题' ],
                 data: ['Top10','Top9','Top8','Top7','Top6','Top5','Top4','Top3','Top2','Top1'],
