                              fontSize:12,
                              verticalAlign: 'middle',
                              position: 'insideRight',
                              color:'#FFFFFF'
                        }
                      },
                      data: [
                          { coord: [1390, 'Top10'], name: '出现语速过快现象'},
                          {coord: [1530, 'Top9'], name: '出现抢话现象'},
                          {coord: [2923, 'Top8'], name: '答案针对性差'},
                          {coord: [3530, 'Top7'], name: '确认不必要信息'},
                          {coord: [3792, 'Top6'], name: '处理出错未纠正'},
                          {coord: [4393, 'Top5'], name: '提醒不到位'},
                          {coord: [4493, 'Top4'], name: '表述不清楚'},
                          {coord: [4560, 'Top3'], name: '禁语/错别字'},
